<script setup lang="ts">
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  ITenantAttribute,
  IDataTypeInstance,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesTextFieldConfiguration } from "./models";
import DatahubBadWordFilter from "../BadWordFilter/DatahubBadWordFilter.vue";

defineProps<{
  attribute: ITenantAttribute;
  dataTypeInstance: IDataTypeInstance<IDataTypesTextFieldConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesTextFieldConfiguration>({
  set: ({ max, min, rows, isEditor, ...rest }) =>
    ({
      max: Number(max),
      min: Number(min),
      rows: Number(rows),
      isEditor: Boolean(isEditor),
      ...rest,
    }) as IDataTypesTextFieldConfiguration,
});
</script>

<template>
  <ACol>
    <ATextField
      v-model.number="configuration.min"
      :label="$t('stringValue.minLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.max"
      :label="$t('stringValue.maxLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.rows"
      :label="$t('textField.rows')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model="configuration.regex"
      :label="$t('stringValue.regex')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.isEditor"
      :label="$t('textField.isEditor')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
  <DatahubBadWordFilter
    v-model="configuration.badwords"
    :scopable="attribute?.scopable ?? dataTypeInstance.scopable"
    :remote-scopes="remoteScopes"
    :multi-language="attribute?.multilanguage ?? dataTypeInstance.multilanguage"
    :remote-languages="remoteLanguages"
    :readonly="readonly"
  />
</template>
